.Navbar-container {
  background: #08090d;
  font-weight: 300;
  display: flex;
  flex-direction: row;
  color: white;
  justify-content: space-between;
  & > button {
    flex: 1 1 auto;
    font-size: 14px;
    font-family: "Roboto Mono", monospace;
    background: none;
    border: none;
    color: white;
    transition: 0.25s;
  }
  & > button + button {
    border-left: solid 1px white;
  }
  & > button:hover {
    cursor: pointer;
    color: #08090d;
    font-weight: 900;
    border-color: white;
    box-shadow: inset 0 -3.25em 0 0 white;
  }
}
