.App {
  text-align: center;
  /* background: #0c0d13; */
  /* background: #1F2232; */
  /* background: #181a26; */
  background: #08090d;

  &>div {
    /* background: -moz-linear-gradient(0deg, rgba(24,26,38,1) 10%, rgba(8,9,13,1) 100%);
background: -webkit-linear-gradient(0deg, rgba(24,26,38,1) 10%, rgba(8,9,13,1) 100%);
background: linear-gradient(0deg, rgba(24,26,38,1) 10%, rgba(8,9,13,1) 100%); */
    background: url('./Images/gradient(2).png');
    background-size: cover;
  }

  overflow-x: hidden;
}

.Navbar-wrapper {
  background: #08090d;
  z-index: 1000;
  font-weight: 300;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  position: absolute;
  bottom: 0;
  height: 3rem;
  transition: height 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.Navbar-wrapper,
.Navbar-container {
  height: 3rem;

}

.Navbar-sticky {
  position: fixed;
  top: 0;
  bottom: unset;
}

.App-header {
  text-align: left;
  /* font-family: "Major Mono Display", monospace; */
  font-family: "Comfortaa", sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 36px;
  padding: 0 5rem;
  position: relative;
}

.App-header__links-container {
  display: flex;
  column-gap: 5rem;

}

.App-header__link {
  transition: 0.25s;
  --color: #CCD0E0;
  --hover: #CCD0E0;
  padding: 1rem 2rem;
  color: white;
  text-decoration: none;
  box-shadow:
    0.3em 0.3em 0 0 var(--color),
    inset 0.3em 0.3em 0 0 var(--color);

  &:hover,
  &:focus {
    color: #08090d;
    font-weight: 700;
    box-shadow:
      0 0 0 0 var(--hover),
      inset 6em 3.5em 0 0 var(--hover);
  }
}

.App-byline {
  /* font-family: "Roboto Mono", monospace; */
  font-family: "Comfortaa", sans-serif;
  font-weight: 300;
}

.App-footer {
  font-style: italic;
  font-size: 24px;
  font-weight: 900;
}

a {
  font-family: "Roboto Mono", monospace;
  font-weight: 300;
  color: #6b7fd7;
}



@media screen and (max-width: 476px) {
  .App-header {
    font-size: 14px;
  }

  .App-footer {
    font-size: 10px;
  }
}

.App-link {
  color: #61dafb;
}





.About-container {
  flex-direction: row;
}

.About-container,
.Bio-container {
  margin: 0 5rem;
  display: flex;
  color: white;
  /* flex-direction: row; */
  align-items: center;
  margin-top: 3rem;

  /* background: rgb(24, 26, 38); */
  @media screen and (max-width: 1000px) {
    flex-direction: column;
    margin: 0 .5rem;
    align-items: center;
  }
}

.About-content {
  text-align: left;
  flex-basis: 60%;
  font-family: "Roboto Mono", monospace;
  margin: 0;
}

.Skills-content {
  display: flex;
}

.Experience-container {
  margin: 0 10rem;
  display: flex;
  color: white;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
}

.Experience-content-heading {
  display: flex;
  flex-direction: column;

  &>strong {
    font-size: 18px;
  }
}



.About-byline {
  flex-basis: 40%;
  font-size: 48px;
  font-family: "Major Mono Display", monospace;

  font-weight: 300;
  text-align: left;
  padding-left: 7rem;

  @media screen and (max-width: 1000px) {
    padding-left: unset;
    text-align: center;
  }

  &>strong {
    font-weight: 900
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.divider {
  margin: 5rem 10rem;
}

.Contact-footer {
  justify-content: center;
  display: flex;
  flex-direction: row;
  padding-bottom: 5rem;

  &>strong {
    font-size: 24px;
    font-family: "Roboto Mono", monospace;
    background: none;
    border: none;
    padding: 0px 36px;
    margin: 12px 0;
    color: white;
  }
}

.arrows {
  width: 60px;
  height: 72px;
  position: absolute;
  left: 50%;
  margin-left: -30px;
  bottom: 20px;
}

.arrows path {
  stroke: #2994d1;
  fill: transparent;
  stroke-width: 1px;
  animation: arrow 2s infinite;
  -webkit-animation: arrow 2s infinite;
}

@keyframes arrow {
  0% {
    opacity: 0;
  }

  40% {
    opacity: 1;
  }

  80% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

@-webkit-keyframes arrow

/*Safari and Chrome*/
  {
  0% {
    opacity: 0;
  }

  40% {
    opacity: 1;
  }

  80% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

.arrows path.a1 {
  animation-delay: -1s;
  -webkit-animation-delay: -1s;
  /* Safari 和 Chrome */
}

.arrows path.a2 {
  animation-delay: -0.5s;
  -webkit-animation-delay: -0.5s;
  /* Safari 和 Chrome */
}

.arrows path.a3 {
  animation-delay: 0s;
  -webkit-animation-delay: 0s;
  /* Safari 和 Chrome */
}